	background: linear-gradient(-190deg, #4A4EA4 65%, #E884A1);
}

.theme-btn{
	background:-webkit-linear-gradient(top,rgba(255,183,50,1),rgba(250,140,25,1)) !important;
	background:linear-gradient(180deg,rgba(255,183,50,1),rgba(250,140,25,1)) !important;
	box-shadow:0px -1px 0px 0px rgba(255,166,0,0.55), 0px 1px 0px 0px rgba(245,245,245,0.6) !important;
	border-radius:4px !important;
	border: 0 !important;
}

.paginationRight16{
	margin-right: 16px !important;
}

.theme-gray{
	color: #84540e;
}

.theme-btn-green{
	background: #46c46f !important;
	border: 0 !important;
	color: #fff !important;
}

.theme-btn-gray{
	background: #999 !important;
	border: 0 !important;
	color: #fff !important;
}

.theme-btn-red{
	background: #cf1322 !important;
	border: 0 !important;
	color: #fff !important;
}

.theme-btn-green:hover, .theme-btn-green:active{
	opacity: .8 !important;
