* {
  padding: 0;
  margin: 0;
}
body #root {
  height: 100%;
}
.ant-menu-dark,
.ant-menu-dark .ant-menu-sub {
  background: #333 !important;
}
.ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background: #2b313a !important;
}
.rex-scroll-bar {
  -webkit-overflow-scrolling: touch;
  /*滚动条样式*/
}
.rex-scroll-bar::-webkit-scrollbar {
  width: 3px;
  display: none;
}
.rex-scroll-bar::-webkit-scrollbar-thumb {
  border-radius: 1.5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #F79F17;
}
.rex-scroll-bar::-webkit-scrollbar-track {
  border-radius: 1.5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #EDEDED;
}
.rex-scroll-bar:hover::-webkit-scrollbar {
  display: block;
}
